import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController } from 'ionic-angular';
import * as providers from '../../providers';

/**
 * Generated class for the PasswordPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage({
  name: 'PasswordPage',
  segment: 'password'
})
@Component({
  selector: 'page-password',
  templateUrl: 'password.html',
})
export class PasswordPage {

  formData: any;
  
  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public alertCtrl: AlertController,
              private loading: providers.AppLoadingProvider,
              private principal: providers.PrincipalProvider,
              private passwordService: providers.PasswordServiceProvider) {
      this.formData = {};
  }

  doSave() {
    if (this.formData.password !== this.formData.confirmPassword) {
      let alert = this.alertCtrl.create({
        title: 'エラー',
        subTitle: 'パスワードが一致しません。',
        buttons: ['OK']
      });
      alert.present();
      return;
    }

    this.loading.start(() => {
      
      return this.passwordService.save(this.formData.password).toPromise()
      .then(() => {
        
        let alert = this.alertCtrl.create({
          title: '情報',
          subTitle: 'パスワードを変更しました。',
          buttons: ['OK']
        });
        alert.present();
        
      }).catch((response) => {
        let alert = this.alertCtrl.create({
          title: 'エラー',
          subTitle: 'パスワード変更に失敗しました。',
          buttons: ['OK']
        });
        alert.present();
      });
    });
  }
}



